const tagArr = ref<TagEvent[]>([])

const listenTag = (row) => {
  //Êü•ÊâætagArr‰∏≠ÊòØÂê¶Â≠òÂú®id
  const index = tagArr.value.findIndex((item) => item.id === row.id);
  if (index === -1) {
    tagArr.value.push(row);
    return true
  } else {
    //Â¶ÇÊûúÂ≠òÂú®ÔºåÂàôÂ∞ÜnumÂä†1
    tagArr.value.splice(index, 1)
    return false
  }
}

// UTCÊ†ºÂºèÂåñÊàêÊú¨Âú∞Êó∂Èó¥
const formatLocalTime = (
    timeStr: string,
    formatter = "YYYY-MM-DD HH:mm:ss"
) => {
  if (!(timeStr && dayjs(timeStr))) return "";
  // ‰ΩøÁî® Day.js ÁöÑ parse ÂáΩÊï∞Â∞Ü FullCalendar Êó∂Èó¥Ëß£Êûê‰∏∫ Day.js ÂØπË±°
  const parsedTime = dayjs(timeStr);
  //ÊòæÁ§∫‰∏≠ÂõΩÊó∂Èó¥ÂáèÂéª8Â∞èÊó∂
  const localTime = parsedTime.subtract(8, "hours");

  // Â∞Ü Day.js ÂØπË±°Ê†ºÂºèÂåñ‰∏∫ÊâÄÈúÄÁöÑÊ†ºÂºèÔºå‰æãÂ¶Ç 'YYYY-MM-DD HH:mm:ss'
  const formattedTime = localTime.format("YYYY-MM-DD HH:mm:ss");

  return formattedTime;
};
